export default {
	data() {
		return {
			input_keyWords: '',
			status:false,
			btn:'添加',
			users:{},
			result:[],
			friends: [],
			recommends: [],
			friendTip:'',
			add:[],
		};
	},
	onLoad: function() {
		// this.recommendFriend();
		// this.addRecommendFriend(toId)
		var that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.users = res.data;
				console.log(that.users);
				that.recommendFriend();
			}
		});
	},
	methods: {
		search: function() {
			uni.request({
				url: this.$baseUrl + '/friend/keywords',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					fromId: this.users.id,
					keyWords: this.input_keyWords
				},
				success: res => {
					if (res.data.code ===1 ) {
						console.log(res.data.data);
						this.friends = res.data.data;
					}else{
						uni.showToast({
							title:res.data.msg
						})
					}
				}
			});
		},
		recommendFriend: function() {
			uni.request({
				url: this.$baseUrl + '/friend/recommend',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: {
					fromId: this.users.id
				},
				success: res => {
					console.log(JSON.stringify(res.data.data));
					if (res.data.code === 1) {
						this.recommends = res.data.data;
						for(var i =0; i <this.recommends.length;i++){
							this.add[i] = false
						}
					}
				}
			});
		},
		changeBtn:function(index){
			// if(this.add[index]){
			// 	// this.status = true;
			// 	this.add.splice(index,1,true);
			// }else{
			// 	// this.status = false
			// 	this.add.splice(index,1,false)
			// }
			this.add.splice(index,1,true);
			
			
			
		},
		addRecommendFriend(toId, index) {
			this.changeBtn(index)
			uni.request({
				url: this.$baseUrl + '/friend/friend',
				method: 'POST',
				data: {
					fromId: this.users.id,
					toId: toId,
				},
				success: res => {
					if (res.data.msg ==='成功') {
						uni.showToast({
							title: res.data.msg
						});
					} else {
						uni.showToast({
							title:res.data.msg
						})
					}
				}
			});
		},
		addFriend(toId){
			uni.request({
				url: this.$baseUrl + '/friend/friend',
				method: 'POST',
				data: {
					fromId: this.users.id,
					toId: toId,
				},
				success: res => {
					if (res.data.code ===1) {
						if(this.friends.friendFlag ===0){
							this.friendTip ='你们已经成为好友';
						}else{
							this.friendTip =''
						};
						uni.showToast({
							title: res.data.msg
						});
					} else {
							uni.showToast({
								title:res.data.msg
							})
					}
				}
			});
		}
	}
};
